// Variables
$base__color-text: $palette-cod-gray !default;

$base__text-font-size: 15px !default;
$base__text-line-height: 26px !default;
$base__text-font-family: $font-primary !default;

// Styles
// Notes:
// - The flex styles applied to html, body and main are intended to force the footer to the bottom of an empty page
html {
  display: flex;
  min-height: 100%;
  flex-direction: column;
}

body {
  display: flex;
  min-height: 100%;
  flex-direction: column;
  flex-grow: 1;
  font-family: $base__text-font-family;
  color: $base__color-text;
  @include rfs($base__text-font-size, font-size);
  @include rfs($base__text-line-height, line-height);
}

main {
  flex-grow: 1;
}
