// Variables
$footer__color-bg: $palette-alabaster !default;
$footer__color-link-text: $palette-cod-gray !default;
$footer__color-link-text-hover: $palette-limeade2 !default;
$footer__color-copyright-area-text: $palette-cod-gray !default;

$footer__text-link-font-family: $font-primary !default;
$footer__text-link-font-size: 15px !default;
$footer__text-link-line-height: 20px !default;
$footer__text-link-letter-spacing: 0 !default;
$footer__text-link-font-weight: $font-weight-light !default;

$footer__text-copyright-area-font-family: $font-primary !default;
$footer__text-copyright-area-font-size: 14px !default;
$footer__text-copyright-area-line-height: 20px !default;
$footer__text-copyright-area-letter-spacing: 0 !default;
$footer__text-copyright-area-font-weight: $font-weight-light !default;

// Styles
.footer {
  background: $footer__color-bg;

  &__top-wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 32px 22px 0 22px;
    align-items: flex-start;

    @include media-breakpoint-up(md) {
      flex-direction: row;
    }
  }

  &__logo-link {
    transition: $transition-normal;

    &:hover,
    &:focus {
      opacity: 0.5;
    }
  }

  &__logo-image {
    max-width: 170px;
  }

  &__other-links {
    margin: 24px 0;

    @include media-breakpoint-up(md) {
      order: 2;
      margin: 0;
    }
  }

  &__button-link-list {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__button-link-item {
    display: block;
    margin: 10px 0 0 0;
    padding: 0;

    &:first-child {
      margin-top: 0;
    }
  }

  &__social-link-list {
    display: flex;
    list-style: none;
    margin: 20px 0 0 0;
    padding: 0;
  }

  &__social-link-item {
    display: block;
    margin-left: 10px;

    &:first-child {
      margin-left: 0;
    }
  }

  &__links {
    display: flex;
    flex-direction: column;
    margin-right: auto;

    @include media-breakpoint-up(md) {
      margin-left: auto;
    }

    @include media-breakpoint-up(lg) {
      flex-direction: row;
      justify-content: space-around;
      flex-grow: 1;
      padding: 0 20px;
    }
  }

  &__link-list {
    display: flex;
    flex-direction: column;
    list-style: none;
    margin: 0;
    padding: 0;

    &--secondary {
      margin-top: 5px;

      @include media-breakpoint-up(lg) {
        margin: 0;
      }
    }
  }

  &__link-item {
    display: block;
    margin-top: 5px;

    &:first-child {
      margin-top: 0;
    }
  }

  &__link {
    color: $footer__color-link-text;
    font-family: $footer__text-link-font-family;
    font-size: $footer__text-link-font-size;
    font-weight: $footer__text-link-font-weight;
    line-height: $footer__text-link-line-height;
    letter-spacing: $footer__text-link-letter-spacing;
    transition: $transition-normal;

    &:hover,
    &:focus {
      color: $footer__color-link-text-hover;
      text-decoration: none;
    }
  }

  &__bottom-wrapper {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 40px 0 10px 0;
    margin-bottom: 100px;
    align-items: center;
    position: relative;
  }

  &__copyright-area {
    color: $footer__color-copyright-area-text;
    font-family: $footer__text-copyright-area-font-family;
    font-size: $footer__text-copyright-area-font-size;
    font-weight: $footer__text-copyright-area-font-weight;
    line-height: $footer__text-copyright-area-line-height;
    letter-spacing: $footer__text-copyright-area-letter-spacing;
  }

  &__copyright-area-text {
    display: inline-block;
  }

  &__copyright-area-link-list {
    display: inline-block;
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__copyright-area-link-item {
    display: inline-block;
    margin: 0;
    padding: 0 0 0 30px;
    position: relative;

    &:before {
      content: "";
      height: 12px;
      width: 1px;
      display: block;
      position: absolute;
      left: 14px;
      top: 50%;
      margin-top: -6px;
      background: $footer__color-copyright-area-text;
    }
  }

  &__copyright-area-link {
    color: $footer__color-link-text;
    transition: $transition-normal;

    &:hover,
    &:focus {
      color: $footer__color-link-text-hover;
      text-decoration: none;
    }
  }

  &__decor-image {
    margin-top: 28px;

    @include media-breakpoint-up(lg) {
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }
}
