$page-section__color-color-normal-tint: $palette-white !default;
$page-section__color-color-normal-text: inherit !default;

$page-section__color-color-light-tint: $palette-alabaster !default;
$page-section__color-color-light-text: inherit !default;

$page-section__color-color-primary-tint: $palette-pistachio !default;
$page-section__color-color-primary-text: $palette-white !default;

$page-section__color-color-secondary-tint: $palette-froly !default;
$page-section__color-color-secondary-text: $palette-white !default;

$page-section__color-color-tertiary-tint: $palette-robins-egg-blue !default;
$page-section__color-color-tertiary-text: $palette-white !default;

$page-section__image-style-textured: "/images/Greenwood/modules/page-section/styleTexturedBg.jpg" !default;
$page-section__image-decor-side-left-primary: "/images/Greenwood/modules/page-section/decorSideLeftPrimary.png" !default;
$page-section__image-decor-side-right-primary: "/images/Greenwood/modules/page-section/decorSideRightPrimary.png" !default;
$page-section__image-decor-side-left-secondary: "/images/Greenwood/modules/page-section/decorSideLeftSecondary.png" !default;
$page-section__image-decor-side-right-secondary: "/images/Greenwood/modules/page-section/decorSideRightSecondary.png" !default;
$page-section__image-decor-side-left-tertiary: "/images/Greenwood/modules/page-section/decorSideLeftTertiary.png" !default;
$page-section__image-decor-side-right-tertiary: "/images/Greenwood/modules/page-section/decorSideRightTertiary.png" !default;
$page-section__image-decor-top-edge-primary: "/images/Greenwood/modules/page-section/decorTopEdgePrimary.svg" !default;

$page-section__metric-decor-side-margin: 54px !default;

// Sets `color` to match the tint color of whatever `page-section` we are in
@mixin pae-section__set-color-to-tint-color {
  .page-section__content--color-normal & {
    color: $page-section__color-color-normal-tint;
  }

  .page-section__content--color-light & {
    color: $page-section__color-color-light-tint;
  }

  .page-section__content--color-primary & {
    color: $page-section__color-color-primary-tint;
  }

  .page-section__content--color-secondary & {
    color: $page-section__color-color-secondary-tint;
  }

  .page-section__content--color-tertiary & {
    color: $page-section__color-color-tertiary-tint;
  }
}

// Styles
.page-section {
  $root: &;

  // Make sequential page-sections overlap, so that our decor top/bottom edges look nice unless we specify that we want space
  & + & {
    margin-top: -54px;
  }

  &__content {
    width: 100%;
    position: relative;
    padding-bottom: 54px;
    background-repeat: repeat;
    background-attachment: fixed;
    overflow-x: hidden;

    #{$root}:last-child & {
      padding-bottom: 0;
    }

    &:before {
      content: "";
      display: none;
      position: absolute;
      top: 0;
      right: 50%;
      bottom: 0;
      width: 100vw;
      background-position: center right;
      background-repeat: no-repeat;

      // Iterate over all of bootstraps breakpoints and container max widths and
      // define breakpoint-specific styles using that breakpoint's container max width
      @each $breakpoint, $container-max-width in $container-max-widths {
        @include media-breakpoint-up($breakpoint) {
          display: block;
          margin-right: $container-max-width / 2 + $page-section__metric-decor-side-margin;
        }
      }
    }

    &:after {
      content: "";
      display: none;
      position: absolute;
      top: 0;
      left: 50%;
      bottom: 0;
      width: 100vw;
      background-position: center left;
      background-repeat: no-repeat;

      // Iterate over all of bootstraps breakpoints and container max widths and
      // define breakpoint-specific styles using that breakpoint's container max width
      @each $breakpoint, $container-max-width in $container-max-widths {
        @include media-breakpoint-up($breakpoint) {
          display: block;
          margin-left: $container-max-width / 2 + $page-section__metric-decor-side-margin;
        }
      }
    }

    &--style-textured {
      background-blend-mode: multiply;
      background-image: url($page-section__image-style-textured);

      @include ie11 {
        background-image: none;
      }
    }

    &--color-normal {
      background-color: $page-section__color-color-normal-tint;
      color: $page-section__color-color-normal-text;
    }

    &--color-light {
      background-color: $page-section__color-color-light-tint;
      color: $page-section__color-color-light-text;
    }

    &--color-primary {
      background-color: $page-section__color-color-primary-tint;
      color: $page-section__color-color-primary-text;
    }

    &--color-secondary {
      background-color: $page-section__color-color-secondary-tint;
      color: $page-section__color-color-secondary-text;
    }

    &--color-tertiary {
      background-color: $page-section__color-color-tertiary-tint;
      color: $page-section__color-color-tertiary-text;
    }

    &--decor-sides-primary {
      &:before {
        background-image: url($page-section__image-decor-side-left-primary);
      }

      &:after {
        background-image: url($page-section__image-decor-side-right-primary);
      }
    }

    &--decor-sides-secondary {
      &:before {
        background-image: url($page-section__image-decor-side-left-secondary);
      }

      &:after {
        background-image: url($page-section__image-decor-side-right-secondary);
      }
    }

    &--decor-sides-tertiary {
      &:before {
        background-image: url($page-section__image-decor-side-left-tertiary);
      }

      &:after {
        background-image: url($page-section__image-decor-side-right-tertiary);
      }
    }

    &--decor-top-edge-primary {
      mask-border: url($page-section__image-decor-top-edge-primary) 54 repeat;
      mask-border-width: 54px 0 0 0;
      padding-top: 54px;
    }
  }

  &__fluid-container {
    @include media-breakpoint-up(lg) {
      display: flex;
      position: relative;
      z-index: 1;
    }
  }

  &__fluid-content-fluid {
    min-height: 200px;
    position: relative;

    @include media-breakpoint-up(md) {
      min-height: 300px;
    }

    @include media-breakpoint-up(lg) {
      width: 50vw;
      min-height: 0;
    }
  }

  &__fluid-content-normal {
    @include media-breakpoint-up(lg) {
      width: map-get($container-max-widths, lg) / 2;

      &--left {
        margin-left: calc(50vw - #{map-get($container-max-widths, lg) / 2});
      }
    }

    @include media-breakpoint-up(xl) {
      width: map-get($container-max-widths, xl) / 2;

      &--left {
        margin-left: calc(50vw - #{map-get($container-max-widths, xl) / 2});
      }
    }
  }
}
