.banner {
  display: flex;
  flex-direction: column;

  &__col {
    width: 100%;
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: auto;

    @include media-breakpoint-up(md) {
      width: 50%;
    }

    .page-section {
      height: 100%;
      min-height: 190px;

      @include media-breakpoint-up(md) {
        min-height: 379px;
      }

      &__content {
        height: 100%;
        min-height: 190px;

        @include media-breakpoint-up(md) {
          min-height: 379px;
        }
      }
    }
  }

  &__image-full {
    overflow: hidden;
    background-size: cover;
    background-position: center;
    height: 190px;
    width: auto;
    max-width: 100%;

    @include media-breakpoint-up(md) {
      height: 379px;
    }
  }
}
