$slick-font-path: $path-fonts;
$slick-loader-path: $imagesPath;
@import "~slick-carousel/slick/slick.scss";
@import "~slick-carousel/slick/slick-theme.scss";

$carousel__metric-arrow-margin: 15px;
$carousel__metric-arrow-size: 40px;

.carousel-slider {
  &__wrapper {
    display: flex;
    align-items: center;
  }

  &__arrow {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 100%;
    padding: $carousel__metric-arrow-size / 2;
    width: $carousel__metric-arrow-size;
    height: $carousel__metric-arrow-size;

    &--color {
      &-primary {
        color: $palette-white;
        background-color: $palette-sulu;
      }

      &-secondary {
        color: $palette-white;
        background-color: $palette-froly;
      }
    }

    &--next {
      margin-left: $carousel__metric-arrow-margin;
    }

    &--prev {
      margin-right: $carousel__metric-arrow-margin;
    }
  }
}
