@mixin media-breakpoint-xxs-only {
  @media (max-width: 413px) {
    @content;
  }
}

// TODO: Going to remove the below
@mixin breakpoint($point) {
  @if $point == xl {
    @media (min-width: 1600px) {
      @content;
    }
  } @else if $point == lg2 {
    @media (min-width: 1280px) {
      @content;
    }
  } @else if $point == lg {
    @media (min-width: 1140px) {
      @content;
    }
  } @else if $point == md2 {
    @media (min-width: 1024px) {
      @content;
    }
  } @else if $point == md {
    @media (min-width: 768px) {
      @content;
    }
  } @else if $point == sm3 {
    @media (max-width: 768px) {
      @content;
    }
  } @else if $point == sm2 {
    @media (min-width: 480px) {
      @content;
    }
  } @else if $point == sm {
    @media (min-width: 375px) {
      @content;
    }
  } @else if $point == smMax {
    @media (max-width: 375px) {
      @content;
    }
  }
}
