// Variables
$cta-button__color-style-primary-bg: $palette-pistachio !default;
$cta-button__color-style-primary-text: $palette-white !default;
$cta-button__color-style-primary-bg-hover: $palette-limeade !default;

$cta-button__color-style-primary-inverse-text-hover: $palette-limeade !default;
$cta-button__color-style-primary-inverse-bg-hover: $palette-frost !default;

$cta-button__color-style-primary-icon-text: $palette-pistachio !default;
$cta-button__color-style-primary-icon-text-hover: $palette-limeade !default;

$cta-button__color-style-secondary-bg: $palette-froly !default;
$cta-button__color-style-secondary-text: $palette-white !default;
$cta-button__color-style-secondary-bg-hover: $palette-cabaret !default;

$cta-button__color-style-secondary-inverse-text-hover: $palette-cabaret !default;
$cta-button__color-style-secondary-inverse-bg-hover: $palette-lavender-blush !default;

$cta-button__color-style-secondary-icon-text: $palette-froly !default;
$cta-button__color-style-secondary-icon-text-hover: $palette-cabaret !default;

$cta-button__color-style-tertiary-bg: $palette-medium-red-violet !default;
$cta-button__color-style-tertiary-text: $palette-white !default;
$cta-button__color-style-tertiary-bg-hover: $palette-violet-eggplant !default;

$cta-button__color-style-tertiary-inverse-text-hover: $palette-violet-eggplant !default;
$cta-button__color-style-tertiary-inverse-bg-hover: $palette-lavender-blush !default;

$cta-button__color-style-tertiary-icon-text: $palette-medium-red-violet !default;
$cta-button__color-style-tertiary-icon-text-hover: $palette-violet-eggplant !default;

$cta-button__color-style-light-bg: $palette-tusk !default;
$cta-button__color-style-light-text: $palette-cod-gray !default;
$cta-button__color-style-light-text-hover: $palette-cod-gray !default;
$cta-button__color-style-light-bg-hover: $palette-sulu !default;

$cta-button__metric-border-width: 1px !default;
$cta-button__metric-border-radius: 17px !default;
$cta-button__metric-padding-v: 8px !default;
$cta-button__metric-padding-h: 17px !default;
$cta-button__metric-size-medium-icon-only-size: 40px !default;
$cta-button__metric-size-small-icon-only-size: 30px !default;
$cta-button__metric-size-large-icon-only-size: 60px !default;

$cta-button__text-font-family: $font-secondary !default;
$cta-button__text-style-outline-variant-font-family: $font-primary !default;
$cta-button__text-style-outline-variant-text-transform: uppercase !default;
$cta-button__text-style-light-font-family: $font-primary !default;
$cta-button__text-size-medium-font-size: 17px !default;
$cta-button__text-size-small-font-size: 14px !default;
$cta-button__text-size-large-font-size: 21px !default;

// Styles
.cta-button {
  $root: &;
  display: inline-flex;
  align-items: stretch;

  &--stretch {
    width: 100%;
  }

  &__text {
    position: relative;
    z-index: 1;

    &--with-icon {
      margin-left: 10px;
    }
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 1;
    transform: translateY(1px);

    &--big {
      transform: scale(1.3) translateY(1px);
    }

    &--style-primary-icon {
      background: $cta-button__color-style-primary-bg;
      border-radius: 100%;
      color: $cta-button__color-style-primary-text;
      font-size: 60%;
      padding: 7px;
      transition: $transition-normal;
    }

    &--style-secondary-icon {
      background: $cta-button__color-style-secondary-bg;
      border-radius: 100%;
      color: $cta-button__color-style-secondary-text;
      font-size: 60%;
      padding: 7px;
      transition: $transition-normal;
    }

    &--style-tertiary-icon {
      background: $cta-button__color-style-tertiary-bg;
      border-radius: 100%;
      color: $cta-button__color-style-tertiary-text;
      font-size: 60%;
      padding: 7px;
      transition: $transition-normal;
    }

    &--icon-only {
      line-height: 0;
    }

    &--icon-only-size-small {
      @include rfs($cta-button__metric-size-small-icon-only-size, width);
      @include rfs($cta-button__metric-size-small-icon-only-size, height);
    }

    &--icon-only-size-medium {
      @include rfs($cta-button__metric-size-medium-icon-only-size, width);
      @include rfs($cta-button__metric-size-medium-icon-only-size, height);
    }

    &--icon-only-size-large {
      @include rfs($cta-button__metric-size-large-icon-only-size, width);
      @include rfs($cta-button__metric-size-large-icon-only-size, height);
    }
  }

  &__button {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    font-family: $cta-button__text-font-family;
    @include rfs($cta-button__metric-border-radius, border-radius);
    @include rfs($cta-button__metric-padding-v, padding-top);
    @include rfs($cta-button__metric-padding-v, padding-bottom);
    @include rfs($cta-button__metric-padding-h, padding-left);
    @include rfs($cta-button__metric-padding-h, padding-right);
    position: relative;
    overflow: hidden;
    line-height: 1;
    transition: $transition-normal;

    &:after {
      content: "";
      width: 0%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      transition: $transition-normal;
    }

    &:hover,
    &:focus {
      text-decoration: none;

      &:after {
        width: 100%;
      }
    }

    &--icon-only {
      padding: 0;
      border-radius: 1000px;
    }

    &--style-primary {
      background: $cta-button__color-style-primary-bg;
      color: $cta-button__color-style-primary-text;

      &:hover,
      &:focus {
        color: $cta-button__color-style-primary-text;

        &:after {
          background: $cta-button__color-style-primary-bg-hover;
        }
      }
    }

    &--style-primary-inverse {
      background: $cta-button__color-style-primary-text;
      color: $cta-button__color-style-primary-bg;

      &:hover,
      &:focus {
        color: $cta-button__color-style-primary-inverse-text-hover;

        &:after {
          background: $cta-button__color-style-primary-inverse-bg-hover;
        }
      }
    }

    &--style-primary-outline {
      background: transparent;
      border-width: $cta-button__metric-border-width;
      border-style: solid;
      border-color: $cta-button__color-style-primary-bg;
      color: $cta-button__color-style-primary-bg;

      &:hover,
      &:focus {
        color: $cta-button__color-style-primary-text;

        &:after {
          background: $cta-button__color-style-primary-bg;
        }
      }
    }

    &--style-primary-outline-variant {
      background: transparent;
      border-width: $cta-button__metric-border-width;
      border-style: solid;
      border-color: $cta-button__color-style-primary-bg;
      color: $cta-button__color-style-primary-bg;
      font-family: $cta-button__text-style-outline-variant-font-family;
      text-transform: $cta-button__text-style-outline-variant-text-transform;

      &:hover,
      &:focus {
        color: $cta-button__color-style-primary-text;

        &:after {
          background: $cta-button__color-style-primary-bg;
        }
      }
    }

    &--style-primary-icon {
      background: transparent;
      color: $cta-button__color-style-primary-icon-text;
      padding: 0 0 2px 0;

      &:hover,
      &:focus {
        color: $cta-button__color-style-primary-icon-text-hover;

        #{$root}__icon {
          background: $cta-button__color-style-primary-icon-text-hover;
        }
      }
    }

    &--style-secondary {
      background: $cta-button__color-style-secondary-bg;
      color: $cta-button__color-style-secondary-text;

      &:hover,
      &:focus {
        color: $cta-button__color-style-secondary-text;

        &:after {
          background: $cta-button__color-style-secondary-bg-hover;
        }
      }
    }

    &--style-secondary-inverse {
      background: $cta-button__color-style-secondary-text;
      color: $cta-button__color-style-secondary-bg;

      &:hover,
      &:focus {
        color: $cta-button__color-style-secondary-inverse-text-hover;

        &:after {
          background: $cta-button__color-style-secondary-inverse-bg-hover;
        }
      }
    }

    &--style-secondary-outline {
      background: transparent;
      border-width: $cta-button__metric-border-width;
      border-style: solid;
      border-color: $cta-button__color-style-secondary-bg;
      color: $cta-button__color-style-secondary-bg;

      &:hover,
      &:focus {
        color: $cta-button__color-style-secondary-text;

        &:after {
          background: $cta-button__color-style-secondary-bg;
        }
      }
    }

    &--style-secondary-outline-variant {
      background: transparent;
      border-width: $cta-button__metric-border-width;
      border-style: solid;
      border-color: $cta-button__color-style-secondary-bg;
      color: $cta-button__color-style-secondary-bg;
      font-family: $cta-button__text-style-outline-variant-font-family;
      text-transform: $cta-button__text-style-outline-variant-text-transform;

      &:hover,
      &:focus {
        color: $cta-button__color-style-secondary-text;

        &:after {
          background: $cta-button__color-style-secondary-bg;
        }
      }
    }

    &--style-secondary-icon {
      background: transparent;
      color: $cta-button__color-style-secondary-icon-text;
      padding: 0 0 2px 0;

      &:hover,
      &:focus {
        color: $cta-button__color-style-secondary-icon-text-hover;

        #{$root}__icon {
          background: $cta-button__color-style-secondary-icon-text-hover;
        }
      }
    }

    &--style-tertiary {
      background: $cta-button__color-style-tertiary-bg;
      color: $cta-button__color-style-tertiary-text;

      &:hover,
      &:focus {
        color: $cta-button__color-style-tertiary-text;

        &:after {
          background: $cta-button__color-style-tertiary-bg-hover;
        }
      }
    }

    &--style-tertiary-inverse {
      background: $cta-button__color-style-tertiary-text;
      color: $cta-button__color-style-tertiary-bg;

      &:hover,
      &:focus {
        color: $cta-button__color-style-tertiary-inverse-text-hover;

        &:after {
          background: $cta-button__color-style-tertiary-inverse-bg-hover;
        }
      }
    }

    &--style-tertiary-outline {
      background: transparent;
      border-width: $cta-button__metric-border-width;
      border-style: solid;
      border-color: $cta-button__color-style-tertiary-bg;
      color: $cta-button__color-style-tertiary-bg;

      &:hover,
      &:focus {
        color: $cta-button__color-style-tertiary-text;

        &:after {
          background: $cta-button__color-style-tertiary-bg;
        }
      }
    }

    &--style-tertiary-outline-variant {
      background: transparent;
      border-width: $cta-button__metric-border-width;
      border-style: solid;
      border-color: $cta-button__color-style-tertiary-bg;
      color: $cta-button__color-style-tertiary-bg;
      font-family: $cta-button__text-style-outline-variant-font-family;
      text-transform: $cta-button__text-style-outline-variant-text-transform;

      &:hover,
      &:focus {
        color: $cta-button__color-style-tertiary-text;

        &:after {
          background: $cta-button__color-style-tertiary-bg;
        }
      }
    }

    &--style-tertiary-icon {
      background: transparent;
      color: $cta-button__color-style-tertiary-icon-text;
      padding: 0 0 2px 0;

      &:hover,
      &:focus {
        color: $cta-button__color-style-tertiary-icon-text-hover;

        #{$root}__icon {
          background: $cta-button__color-style-tertiary-icon-text-hover;
        }
      }
    }

    &--style-light {
      background: $cta-button__color-style-light-bg;
      color: $cta-button__color-style-light-text;
      font-family: $cta-button__text-style-light-font-family;

      &:hover,
      &:focus {
        color: $cta-button__color-style-light-text-hover;

        &:after {
          background: $cta-button__color-style-light-bg-hover;
        }
      }
    }

    &--size-small {
      @include rfs($cta-button__text-size-small-font-size, font-size);
    }

    &--size-medium {
      @include rfs($cta-button__text-size-medium-font-size, font-size);
    }

    &--size-large {
      @include rfs($cta-button__text-size-large-font-size, font-size);
    }
  }
}
