// Ensures an element maintains a particular aspect ratio
// See: https://css-tricks.com/aspect-ratio-boxes/
@mixin aspect-ratio($width, $height) {
  &::before {
    content: "";
    width: 1px;
    margin-left: -1px;
    float: left;
    height: 0;
    padding-top: #{100 / ($height / $width)}#{"%"};
  }
  &::after {
    content: "";
    display: table;
    clear: both;
  }
}
