//Variables
$typography__color-heading-text: $palette-limeade2 !default;
$typography__color-header-bg: $palette-pistachio !default;
$typography__color-header-text: $color-white !default;
$typography__color-row-border: $palette-grey-alto !default;
$typography__metric-row-height: 52px !default;
$typography__metric-row-vertical-padding: 5px !default;
$typography__metric-row-horizontal-padding: 16px !default;
$typography__color-list-bullet: $palette-froly !default;
$typography__text-list-style-type: "\2022" !default;
$typography__metric-ul-indent: 2px !default;
$typography__metric-ul-nested-indent: 10px !default;
$typogrpahy__metric-ol-indent: 2px !default;
$typography__metric-ol-nested-indent: 20px !default;
$typography__color-link-text: $palette-limeade;
$typography__color-link-hover-text: $palette-limeade2;
$typography__metric-link-underline-width: 1.5px;

// Heading styling
$typography__text-headings-map-rfs-property-list: font-size, line-height, margin-bottom;
$typography__text-headings-map: (
  1: (
    font-size: 46px,
    line-height: 50px,
    font-family: $font-secondary,
    font-weight: $font-weight-bold,
    letter-spacing: 0,
    margin-bottom: 18px,
  ),
  2: (
    font-size: 34px,
    line-height: 41px,
    font-family: $font-secondary,
    font-weight: $font-weight-bold,
    letter-spacing: 0,
    margin-bottom: 16px,
  ),
  3: (
    font-size: 30px,
    line-height: 36px,
    font-family: $font-secondary,
    font-weight: $font-weight-bold,
    letter-spacing: 0,
    margin-bottom: 14px,
  ),
  4: (
    font-size: 25px,
    line-height: 35px,
    font-family: $font-secondary,
    font-weight: $font-weight-semi-bold,
    letter-spacing: 0,
    margin-bottom: 8px,
  ),
  5: (
    font-size: 20px,
    line-height: 26px,
    font-family: $font-secondary,
    font-weight: $font-weight-bold,
    letter-spacing: 0,
    margin-bottom: 8px,
  ),
  6: (
    font-size: 18px,
    line-height: 26px,
    font-family: $font-primary,
    font-weight: $font-weight-medium,
    letter-spacing: 0,
    margin-bottom: 6px,
  ),
) !default;

@each $heading-level, $heading-map in $typography__text-headings-map {
  h#{$heading-level},
  .h#{$heading-level} {
    @each $property, $value in $heading-map {
      @if index($typography__text-headings-map-rfs-property-list, $property) {
        @include rfs($value, $property);
      } @else {
        #{$property}: $value;
      }
    }
  }
}

h1, h2, h3, h4, h4, h6 {
  &.custom {
    color: $typography__color-heading-text;
  }
}

// Table styling
table.custom {
  overflow-x: auto;

  th, td {
    padding: $typography__metric-row-vertical-padding $typography__metric-row-horizontal-padding;
  }

  th {
    font-weight: $font-weight-semi-bold;
    background-color: $typography__color-header-bg;
    color: $typography__color-header-text;
  }

  tr {
    font-weight: $font-weight-light;
    height: $typography__metric-row-height;
    border-bottom: solid 1px $typography__color-row-border;

    &:first-child {
      td, th {
        border-top: solid 1px $typography__color-row-border;
      }
    }
  }
}

// Unordered lists
// The UL list bullet takes the colour of the li text, to have a different colour you have to style your own bullet.
ul.custom {
  padding-left: $typography__metric-ul-indent;
  list-style-type: none;
  list-style-position: inside;

  > li {
    display: table-row;

    &:before {
      content: $typography__text-list-style-type;
      color: $typography__color-list-bullet;
      font-size: 20px;
      font-weight: $font-weight-light;
      display: table-cell;
      width: 20px;
    }
  }
}

// Ordered Lists
// NOTE - counters stored in parent elements so wrap
// each UL to get counters to reset correctly!!!
ol.custom {
  padding-left: $typogrpahy__metric-ol-indent;
  list-style-position: inside;
  counter-reset: ol-count;

  > li {
    display: table-row;
    counter-increment: ol-count;

    &:before {
      content: counter(ol-count) ". ";
      display:table-cell;
      padding-right: 10px;
    }
  }

  ol.custom {
    padding-left: $typography__metric-ol-nested-indent;

    > li {
      display:table-row;

      &:before {
        content: counters(ol-count, ".") " ";
        display:table-cell;
        padding-right: 10px;
      }
    }
  }
}

// Nesting an ordered list within an unordered list
ol.custom > li > ul.custom {
  padding-left: $typography__metric-ol-nested-indent
}

// Links
a.custom {
  color: $typography__color-link-text;
  font-weight: $font-weight-normal;
  text-decoration: none;
  border-bottom: solid $typography__metric-link-underline-width $typography__color-link-text;

  &:focus, &:hover {
    color: $typography__color-link-hover-text;
    text-decoration: none;
    border-bottom-color: $typography__color-link-hover-text;
  }
}
