// Dependencies:
// - tile

$content-box__color-style-cta-bg: $palette-alabaster !default;
$content-box__color-style-cta-text: $palette-cod-gray !default;

$content-box__color-style-cta-special-bg: $palette-seashell-peach !default;
$content-box__color-style-cta-special-text: $palette-medium-red-violet !default;

$content-box__color-style-card-bg: $palette-white !default;
$content-box__color-style-card-text: $palette-cod-gray !default;

$content-box__color-style-location-bg: $palette-fuzzy-wuzzy-brown !default;
$content-box__color-style-location-text: $palette-white !default;
$content-box__color-link-text: $palette-white !default;
$content-box__color-hover-text: $palette-white !default;

$content-box__color-style-info-bg: $palette-white !default;
$content-box__color-style-info-text: $palette-cod-gray !default;

.content-box {
  width: 100%;

  &__container {
    $container: &;
    display: flex;
    width: 100%;
    flex-direction: column;
    align-items: center;

    &--style-cta {
      background-color: $content-box__color-style-cta-bg;
      color: $content-box__color-style-cta-text;
      border-radius: $border-radius-normal;
      max-width: 90%;
      margin: 0 auto;
    }

    &--style-cta-special {
      background-color: $content-box__color-style-cta-special-bg;
      color: $content-box__color-style-cta-special-text;
      max-width: 90%;
      margin: 0 auto;
    }

    &--style-cta-special-small {
      background-color: $content-box__color-style-cta-special-bg;
      color: $content-box__color-style-cta-special-text;
      flex-direction: row;
    }

    &--style-card {
      max-width: 350px;
      background-color: $content-box__color-style-card-bg;
      color: $content-box__color-style-card-text;
      border-radius: $border-radius-normal;
      overflow: hidden;
    }

    &--style-location {
      background-color: $content-box__color-style-location-bg;
      color: $content-box__color-style-location-text;
      border-radius: $border-radius-normal;
      overflow: hidden;
      max-width: 550px;

      @include media-breakpoint-up(lg) {
        max-width: 2000px;
      }
    }

    &--style-info {
      background-color: $content-box__color-style-info-bg;
      color: $content-box__color-style-info-text;
      border-radius: $border-radius-normal;
      overflow: hidden;
    }

    @include media-breakpoint-up(lg) {
      flex-direction: row;

      &--style-card, &--style-location {
        flex-direction: column;
      }
    }

    @include media-breakpoint-xxs-only {
      &--style-cta-special-small {
        flex-direction: column;
      }
    }
  }

  &__content-container {
    display: flex;
    align-items: stretch;
    align-self: stretch;
  }

  &__content {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: space-evenly;

    &--style-cta {
      @include rfs(20px, padding-top);
      @include rfs(20px, padding-right);
      @include rfs(25px, padding-bottom);
      @include rfs(20px, padding-left);

      @include media-breakpoint-up(lg) {
        @include rfs(40px, padding-top);
        @include rfs(40px, padding-right);
        @include rfs(50px, padding-bottom);
        @include rfs(40px, padding-left);
      }
    }

    &--style-cta-special {
      @include rfs(20px, padding-top);
      @include rfs(20px, padding-right);
      @include rfs(25px, padding-bottom);
      @include rfs(20px, padding-left);

      @include media-breakpoint-up(lg) {
        @include rfs(40px, padding-top);
        @include rfs(40px, padding-right);
        @include rfs(50px, padding-bottom);
        @include rfs(40px, padding-left);
      }

      @include media-breakpoint-xxs-only {
        padding-top: 0;
        padding-right: 0;
        padding-bottom: 0;
        padding-left: 0;
      }
    }

    &--style-cta-special-small {
      @include rfs(8px, padding-top);
      @include rfs(10px, padding-right);
      @include rfs(8px, padding-bottom);
      @include rfs(10px, padding-left);

      @include media-breakpoint-xxs-only {
        @include rfs(20px, padding-top);
        @include rfs(30px, padding-right);
        @include rfs(20px, padding-bottom);
        @include rfs(30px, padding-left);
      }
    }

    &--style-card {
      @include rfs(15px, padding-top);
      @include rfs(25px, padding-right);
      @include rfs(25px, padding-bottom);
      @include rfs(25px, padding-left);
    }

    &--style-location {
      @include rfs(15px, padding-top);
      @include rfs(25px, padding-right);
      @include rfs(15px, padding-bottom);
      @include rfs(25px, padding-left);
    }

    &--style-info {
      @include rfs(40px, padding-top);
      @include rfs(30px, padding-right);
      @include rfs(40px, padding-bottom);
      @include rfs(30px, padding-left);
    }

    &--content-alignment-center {
      text-align: center;
    }
  }

  &__pre-heading {
    @extend .h4;

    font-weight: $font-weight-normal;
  }

  &__heading {
    &--style-cta-special {
      @extend .h1;
    }

    &--style-cta-special-small {
      @extend .h4;
      margin-bottom: 0;
    }

    &--style-cta {
      @extend .h2;
    }

    &--style-card {
      @extend .h4;
      font-weight: $font-weight-bold;
    }

    &--style-info {
      @extend .h4;

      // Try to determine our colour depending on what page-section we are in
      @include pae-section__set-color-to-tint-color;
    }
  }

  &__body-container {
    display: flex;
    align-items: center;
    flex-grow: 1;
  }

  &__cta-buttons {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    align-self: flex-start;

    &--style-cta {
      @include rfs(20px, margin-top);
      align-items: stretch;

      @include media-breakpoint-up(lg) {
        justify-content: space-between;
      }
    }

    &--style-cta-special {
      @include rfs(20px, margin-top);
    }

    &--style-cta-special-small {
      @include media-breakpoint-xxs-only {
        @include rfs(20px, margin-top);
      }
    }

    &--style-card {
      @include rfs(20px, margin-top);
    }

    &--style-location {
      @include rfs(20px, margin-top);
    }

    &--style-info {
      @include rfs(20px, margin-top);
    }

    &--content-alignment-center {
      align-self: stretch;

      @include media-breakpoint-xxs-only {
        align-self: stretch;
      }
    }

    @include media-breakpoint-up(lg) {
      flex-direction: row;
      align-items: stretch;
      justify-content: flex-start;
    }
  }

  &__cta-button-item {
    display: flex;
    width: 100%;
    align-items: stretch;
    @include rfs(16px, margin-top);

    &:empty {
      display: none;
    }

    &:first-child {
      margin-top: 0;

      @include media-breakpoint-up(lg) {
        margin-left: 0;
      }
    }

    @include media-breakpoint-up(lg) {
      margin-top: 0;
      @include rfs(16px, margin-left);
    }
  }

  &__image {
    $image: &;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    position: relative;

    &--hide-on-mobile {
      display: none;
    }

    &--pop-out {
      @each $size, $length in $spacers {
        &-#{$size} {
          &#{$image}--primary {
            transform: translateX(-$length);
          }

          &#{$image}--secondary {
            transform: translateX($length);
          }
        }
      }
    }

    &--style-cta-special-small {
      @include media-breakpoint-xxs-only {
        width: 100%;
      }
    }

    &--style-info {
      &#{$image}--primary {
        align-self: stretch;

        @include media-breakpoint-up(lg) {
          order: 99;
        }
      }
    }

    @include media-breakpoint-up(lg) {
      display: flex;
    }
  }

  &__location-info {
    display: table;
    @include rfs(6px, padding-top);
    @include rfs(6px, padding-bottom);
  }

  &__location-icon {
    display: table-cell;
    text-align: left;
    min-width: 24px;
    @include rfs(30px, width);
  }

  &__location-link {
    color: $content-box__color-link-text;
    text-decoration: none;

    &:focus, &:hover {
      color: $content-box__color-hover-text;
      text-decoration: underline;
    }
  }
}
