// Variables
$tile__color-image-heading-bg: $palette-concrete !default;
$tile__color-image-heading-bg-hover: $palette-mercury !default;
$tile__color-text: $palette-cod-gray !default;
$tile__color-callout-primary-text: $palette-white !default;
$tile__color-callout-secondary-star-rating: $palette-tree-poppy;
$tile__color-callout-primary-star-rating: $palette-white;

$tile__metric-icon-width: 70%;
$tile__metric-icon-max-width: 174px;

$tile__text-font-family: $font-primary !default;
$tile__text-font-size: 14px !default;
$tile__text-line-height: 24px !default;
$tile__text-letter-spacing: 0 !default;
$tile__text-font-weight: $font-weight-light !default;

$tile__text-heading-font-family: $font-secondary !default;
$tile__text-heading-font-size: 20px !default;
$tile__text-heading-line-height: 24px !default;
$tile__text-heading-letter-spacing: 0 !default;
$tile__text-heading-font-weight: $font-weight-bold !default;

$tile__icon-image-urls: (
  "/images/Greenwood/modules/tile/iconApp.png",
  "/images/Greenwood/modules/tile/iconCurriculum.png",
  "/images/Greenwood/modules/tile/iconSchoolProgram.png",
  "/images/Greenwood/modules/tile/iconActivities.png"
) !default;

$tile__callout-colors: (
  "blue": $palette-robins-egg-blue,
  "pink": $palette-froly,
  "orange": $palette-tree-poppy,
  "green": $palette-pistachio,
) !default;

// Styles
.tile {
  $root: &;

  width: 100%;

  // Callout

  &__callout-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 20em;
  }

  &__callout {
    $callout: &;

    display: flex;
    flex-direction: column;
    padding: 20px;
    height: 16em;
    border-radius: 10px;
    justify-content: center;
    position: relative;

    &--style-primary {
      border: 3px solid map-get($tile__callout-colors, "green");
      color: $tile__color-callout-primary-text;
      @each $color-name, $color in $tile__callout-colors {
        &#{$callout}--color-#{$color-name} {
          background-color: $color;
          border-color: $color;
          &:after {
            display: none;
          }
        }
      }

      .tile__callout-rating {
        color: $tile__color-callout-primary-star-rating;
      }
    }

    &--style-secondary {
      border: 3px solid map-get($tile__callout-colors, "green");
      border-radius: 10px;
      @each $color-name, $color in $tile__callout-colors {
        &#{$callout}--color-#{$color-name} {
          border-color: $color;
        }
      }

      .tile__callout-rating {
        color: $tile__color-callout-secondary-star-rating;
      }
    }

    &:after,
    &:before {
      border-left: 20px solid transparent;
      border-right: 20px solid transparent;
      border-top: 20px solid #fff;
      bottom: -20px;
      content: "";
      left: 50%;
      margin-left: -20px;
      position: absolute;
    }

    // Styling for second triangle (border)
    &:before {
      border-left: 23px solid transparent;
      border-right: 23px solid transparent;
      border-top: 23px solid;
      border-top-color: inherit; // Can't be included in the shorthand to work
      bottom: -23px;
      margin-left: -23px;
    }
  }

  &__callout-text {
    font-size: 0.85em;
    font-weight: 500;

    @include media-breakpoint-up(sm) {
      font-size: 0.65em;
      font-weight: 400;
    }

    @include media-breakpoint-up(lg) {
      font-size: 0.85em;
      font-weight: 500;
    }
  }

  &__callout-rating {
    list-style: none;
    display: flex;
    flex-direction: row;
    padding: 5px 0 0 0;
    color: $tile__color-callout-secondary-star-rating;
    font-size: 1em;

    @include media-breakpoint-up(sm) {
      font-size: 0.8em;
    }

    @include media-breakpoint-up(lg) {
      font-size: 1em;
    }
  }

  &__callout-title {
    margin-top: 15px;
    font-weight: 600;
  }

  // Image

  &__image-wrapper {
    display: block;
    width: 100%;
    border-radius: 10px;
    overflow: hidden;

    &:hover,
    &:focus {
      text-decoration: none;

      #{$root}__image {
        transform: scale(1.2);
      }

      #{$root}__image-heading {
        background: $tile__color-image-heading-bg-hover;
      }
    }
  }

  &__image {
    max-width: 100%;
    height: auto;
    position: relative;
    transition: $transition-zoom;
  }

  &__image-heading {
    display: flex;
    height: 60px;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: relative;
    margin: 0;
    background: $tile__color-image-heading-bg;
    color: $tile__color-text;
    font-family: $tile__text-heading-font-family;
    font-size: $tile__text-heading-font-size;
    font-weight: $tile__text-heading-font-weight;
    line-height: $tile__text-heading-line-height;
    letter-spacing: $tile__text-heading-letter-spacing;
    transition: $transition-normal;
  }

  // Icon

  &__icon-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    position: relative;
  }

  &__icon-icon {
    width: $tile__metric-icon-width;
    max-width: $tile__metric-icon-max-width;
    background-size: contain;

    @include aspect-ratio(1, 1);

    @for $i from 1 through length($tile__icon-image-urls) {
      &--style-#{$i} {
        background-image: url(nth($tile__icon-image-urls, $i));
      }
    }
  }

  &__icon-heading {
    text-align: center;
    position: relative;
    margin: 24px 0 0 0;
    color: $tile__color-text;
    font-family: $tile__text-heading-font-family;
    font-size: $tile__text-heading-font-size;
    font-weight: $tile__text-heading-font-weight;
    line-height: $tile__text-heading-line-height;
    letter-spacing: $tile__text-heading-letter-spacing;

    @include media-breakpoint-up(md) {
      margin-top: 29px;
    }
  }

  &__icon-text {
    margin: 7px 0 0 0;
    color: $tile__color-text;
    font-family: $tile__text-font-family;
    font-size: $tile__text-font-size;
    font-weight: $tile__text-font-weight;
    line-height: $tile__text-line-height;
    letter-spacing: $tile__text-letter-spacing;

    @include media-breakpoint-up(md) {
      margin-top: 11px;
    }

    &--with-button {
      @include media-breakpoint-up(md) {
        min-height: 120px;
      }
    }

    &--with-no-heading {
      margin-top: 14px;
      @include media-breakpoint-up(md) {
        margin-top: 14px;
      }
    }
  }

  &__icon-button {
    margin: 18px 0 36px 0;
  }

  &__icon-whole-tile-link {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom :0;
  }
}
