// Variables
$header__color-bg: $palette-white !default;
$header__color-desktop-line: $palette-mercury !default;
$header__color-desktop-bottom-link-text: $palette-cod-gray !default;
$header__color-desktop-bottom-link-bg-hover: $palette-mercury !default;
$header__color-mobile-button-icon: $palette-pistachio !default;
$header__color-mobile-button-bg-hover: $palette-mercury !default;
$header__color-mobile-link-text: $palette-cod-gray !default;
$header__color-mobile-link-bg-hover: $palette-mercury !default;
$header__color-mobile-link-line: $palette-mercury !default;

$header__metric-desktop-height: 132px !default;
$header__metric-desktop-logo-height: 60px !default;
$header__metric-desktop-logo-ml: 0 !default;
$header__metric-desktop-logo-mr: 16px !default;
$header__metric-desktop-logo-height--lg: 100px !default;
$header__metric-desktop-logo-ml--lg: 16px !default;
$header__metric-desktop-logo-mr--lg: 48px !default;
$header__metric-desktop-top-height: 61px !default;
$header__metric-desktop-bottom-height: 71px !default;
$header__metric-mobile-height: 76px !default;
$header__metric-mobile-logo-height: 54px !default;
$header__metric-mobile-link-height: 54px !default;

$header__text-desktop-bottom-link-font-family: $font-primary !default;
$header__text-desktop-bottom-link-font-size: 14px !default;
$header__text-desktop-bottom-link-line-height: 20px !default;
$header__text-desktop-bottom-link-letter-spacing: 0.5px !default;
$header__text-mobile-button-font-size: 29px !default;
$header__text-mobile-link-font-family: $font-primary !default;
$header__text-mobile-link-font-size: 14px !default;
$header__text-mobile-link-line-height: 20px !default;
$header__text-mobile-link-letter-spacing: 0.5px !default;

// Styles
.header {
  height: $header__metric-mobile-height;

  @include media-breakpoint-up(md) {
    height: $header__metric-desktop-height;
  }

  &__mobile-sticky {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: $header__metric-mobile-height;
    background: $header__color-bg;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.15);
    z-index: 1;

    &--with-top-alert-bar {
      top: $top-alert-bar__metric-height;
    }

    @include media-breakpoint-up(md) {
      display: none;
    }
  }

  &__desktop-sticky {
    display: none;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: $header__metric-desktop-height;
    background: $header__color-bg;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.15);
    z-index: 1;

    @include media-breakpoint-up(md) {
      display: flex;
    }

    &--with-top-alert-bar {
      top: $top-alert-bar__metric-height;
    }
  }

  &__desktop-wrapper {
    display: flex;
    width: 100%;
    align-items: center;
  }

  &__mobile-logo-link {
    display: flex;
    justify-content: center;
    align-items: center;
    height: $header__metric-mobile-height;
    transition: $transition-normal;

    &:hover,
    &:focus {
      opacity: 0.5;
    }
  }

  &__desktop-logo-link {
    display: flex;
    justify-content: center;
    align-items: center;
    height: $header__metric-desktop-height;
    transition: $transition-normal;

    &:hover,
    &:focus {
      opacity: 0.5;
    }
  }

  &__mobile-logo-image {
    height: $header__metric-mobile-logo-height;
    margin-left: 16px;
    margin-right: 20px;
  }

  &__desktop-logo-image {
    height: $header__metric-desktop-logo-height;
    margin-left: $header__metric-desktop-logo-ml;
    margin-right: $header__metric-desktop-logo-mr;

    @include media-breakpoint-up(lg) {
      height: $header__metric-desktop-logo-height--lg;
      margin-left: $header__metric-desktop-logo-ml--lg;
      margin-right: $header__metric-desktop-logo-mr--lg;
    }
  }

  &__desktop-content-wrapper {
    flex-grow: 1;
  }

  &__desktop-top-link-list {
    display: flex;
    height: $header__metric-desktop-top-height;
    justify-content: flex-end;
    align-items: center;
    list-style: none;
    margin: 0;
    padding: 0;
    border-bottom: 1px solid $header__color-desktop-line;
  }

  &__desktop-bottom-link-list {
    display: flex;
    height: $header__metric-desktop-bottom-height;
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__desktop-top-link-item {
    display: block;
    padding-left: 20px;
  }

  &__desktop-bottom-link-item {
    display: block;
    position: relative;
    flex-grow: 1;
  }

  &__desktop-bottom-link {
    display: flex;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    justify-content: center;
    align-items: center;
    color: $header__color-desktop-bottom-link-text;
    padding: 8px;
    text-align: center;
    font-family: $header__text-desktop-bottom-link-font-family;
    font-size: $header__text-desktop-bottom-link-font-size;
    line-height: $header__text-desktop-bottom-link-line-height;
    letter-spacing: $header__text-desktop-bottom-link-letter-spacing;
    background: $header__color-bg;
    transition: $transition-normal;

    &:hover,
    &:focus {
      text-decoration: none;
      color: $header__color-desktop-bottom-link-text;
      background: $header__color-desktop-bottom-link-bg-hover;
    }
  }

  &__mobile-wrapper {
    display: flex;
    width: 100%;
    align-items: center;
  }

  &__mobile-button {
    display: flex;
    width: $header__metric-mobile-height;
    height: $header__metric-mobile-height;
    justify-content: center;
    align-items: center;
    color: $header__color-mobile-button-icon;
    background: $header__color-bg;
    border: 0;
    padding: 0;
    margin-left: auto;
    transition: $transition-normal;
    font-size: $header__text-mobile-button-font-size;

    &:hover,
    &:focus {
      background: $header__color-mobile-button-bg-hover;
      outline: none;
    }
  }

  &__mobile-menu {
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.15);
  }

  &__mobile-link-list {
    display: flex;
    flex-direction: column;
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__mobile-link-item {
    display: block;
    height: $header__metric-mobile-link-height;
    position: relative;
    flex-grow: 1;
    border-top: 1px solid $header__color-mobile-link-line;
  }

  &__mobile-link {
    display: flex;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    justify-content: flex-start;
    align-items: center;
    color: $header__color-mobile-link-text;
    padding: 8px 8px 8px 16px;
    text-align: left;
    font-family: $header__text-mobile-link-font-family;
    font-size: $header__text-mobile-link-font-size;
    line-height: $header__text-mobile-link-line-height;
    letter-spacing: $header__text-mobile-link-letter-spacing;
    background: $header__color-bg;
    transition: $transition-normal;

    &:hover,
    &:focus {
      text-decoration: none;
      color: $header__color-mobile-link-text;
      background: $header__color-mobile-link-bg-hover;
    }
  }
}
