.responsive-image {
  max-width: 100%;

  // Unfortunately we can't use `object-fit` and `object-position` since IE11 doesn't support them
  // Instead we, we insert a <div /> after our <img /> and set its CSS background-image
  // This class is only applied to the <div />
  &--background-image {
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-size: cover;
    background-position: center;
  }

  // This is used in conjunction with the above
  // Is only applied to the <img />
  &--keep-min-height {
    display: none;
  }
}
