$accordion__color-boder: $palette-grey-alto !default;
$accordion__color-icon: $palette-pistachio !default;

$accordion__metric-border-width: 1px !default;
$accordion__metric-link-height: 46px !default;
$accordion__metric-body-margin: 10px !default;
$accordion__metric-icon-size: 12px !default;

.accordion {
  &__item {
    display: flex;
    flex-direction: column;
    border-top: $accordion__metric-border-width solid $accordion__color-boder;

    &:last-child {
      border-bottom: $accordion__metric-border-width solid $accordion__color-boder;
    }
  }

  &__link {
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: initial;
    height: $accordion__metric-link-height;

    &:hover {
      color: initial;
      text-decoration: none;
    }
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__header-text {
    font-weight: $font-weight-medium;
  }

  &__body {
    display: none;
    margin: $accordion__metric-body-margin 0;
  }

  &__icon {

    &--plusminus {
      position: relative;
      width: $accordion__metric-icon-size;
      height: $accordion__metric-icon-size;
      cursor: pointer;

      &.active {
        &:before {
          transform: translatey(-50%) rotate(-90deg);
          opacity: 0;
        }

        &:after {
          transform: translatey(-50%) rotate(0);
        }
      }

      &:before,
      &:after {
        content: "";
        background-color: $accordion__color-icon;
        display: block;
        position: absolute;
        top: 50%;
        left: 0;
        transition: $transition-normal;
        width: 100%;
        height: 3px;
      }

      &:before {
        transform: translatey(-50%);
      }

      &:after {
        transform: translatey(-50%) rotate(90deg);
      }
    }
  }
}
