$content-modal__color-close-text: $palette-pistachio;
$content-modal__color-close-bg: $palette-white;
$content-modal__color-close-text-hover: $palette-limeade;
$content-modal__color-close-bg-hover: $palette-mercury;

$content-modal__metric-icon-icon-width: 70%;
$content-modal__metric-icon-icon-max-width: 100px;

$content-modal__icon-image-urls: (
  "/images/Greenwood/modules/content-modal/iconApp.png",
  "/images/Greenwood/modules/content-modal/iconCurriculum.png",
  "/images/Greenwood/modules/content-modal/iconSchoolProgram.png",
  "/images/Greenwood/modules/content-modal/iconActivities.png"
) !default;

.content-modal {
  &__modal-dialog {
    max-width: calc(100vw - 20px);

    &--icon {
      width: 540px;
    }

    &--image {
      @include media-breakpoint-up(lg) {
        max-width: map-get($grid-breakpoints, lg) - 20px;
      }
    }
  }

  &__modal-content {
    border: none;
    border-radius: $border-radius-normal;
    @include rfs(35px, padding-top);
    @include rfs(40px, padding-right);
    @include rfs(50px, padding-bottom);
    @include rfs(40px, padding-left);

    &--image {
      padding-top: 0;
      padding-right: 0;
      padding-bottom: 0;
      padding-left: 0;
    }
  }

  &__modal-body {
    &--image {
      padding-top: 0;
      padding-right: 0;
      padding-bottom: 0;
      padding-left: 0;
    }
  }

  &__close {
    display: flex;
    width: 45px;
    height: 45px;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 10px;
    right: 10px;
    border-radius: 100%;
    border: none;
    outline: none;
    color: $content-modal__color-close-text;
    background-color: $content-modal__color-close-bg;
    z-index: 1;
    transition: $transition-normal;
    font-size: 20px;

    &:hover, &:focus {
      color: $content-modal__color-close-text-hover;
      background-color: $content-modal__color-close-bg-hover;
    }
  }

  // Icon

  &__icon-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__icon-icon {
    width: $content-modal__metric-icon-icon-width;
    max-width: $content-modal__metric-icon-icon-max-width;
    background-size: contain;

    @include aspect-ratio(1, 1);

    @for $i from 1 through length($content-modal__icon-image-urls) {
      &--style-#{$i} {
        background-image: url(nth($content-modal__icon-image-urls, $i));
      }
    }
  }

  &__icon-heading {
    margin-top: 20px;
    text-align: center;
  }

  // Image

  &__image-image {
    width: 100%;
  }

  &__image-text {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    @include rfs(9px, padding-top);
    @include rfs(40px, padding-right);
    @include rfs(12px, padding-bottom);
    @include rfs(40px, padding-left);
  }
}
