// Paths
$assetsPath: "/";
$cssPath: $assetsPath + "styles/";
$path-fonts: $assetsPath + "styles/fonts/";
$imagesPath: $assetsPath + "images/";
$jsPath: $assetsPath + "scripts/";

// Palette
// Use http://chir.ag/projects/name-that-color/ to get unique names
// Sometimes you may need to use the same name twice - use your discretion for when that's needed
// e.g. If they're very similar, just use the existing same-named color
$palette-white: #ffffff;
$palette-concrete: #f2f2f2;
$palette-alabaster: #f7f7f7;
$palette-gallery: #efefef;
$palette-mercury: #e8e8e8;
$palette-boulder: #757575;
$palette-cod-gray: #171717;
$palette-pistachio: #88c005;
$palette-limeade: #6fa700;
$palette-limeade2: #5b8104;
$palette-tusk: #dff3b2;
$palette-sulu: #c5e86c;
$palette-froly: #f05d77;
$palette-cabaret: #d7445e;
$palette-frost: #eef5db;
$palette-lavender-blush: #ffedf0;
$palette-tree-poppy: #f79421;
$palette-tahiti-gold: #f38608;
$palette-fuzzy-wuzzy-brown: #C94059;
$palette-robins-egg-blue: #02c1de;
$palette-medium-red-violet: #ae2c96;
$palette-violet-eggplant: #95137d;
$palette-seashell-peach: #ffefe7;
$palette-grey-alto: #d8d8d8;

// Fonts
$font-primary: "Poppins", sans-serif;
$font-secondary: "Zilla Slab", serif;

// Font weights
$font-weight-thin: 100;
$font-weight-extra-light: 200;
$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-medium: 500;
$font-weight-semi-bold: 600;
$font-weight-bold: 700;
$font-weight-extra-bold: 800;
$font-weight-black: 900;

// Transitions
$transition-normal: 0.3s;
$transition-zoom: 0.5s ease;

// Border radius
$border-radius-normal: 10px;

// TODO: Going to remove the below
$primary: #2e8ec1;
$primaryDarker: darken($primary, 20%);
$primaryDark: darken($primary, 10%);
$primaryLight: lighten($primary, 10%);
$primaryLighter: lighten($primary, 20%);

$secondary: #945022;
$secondaryDarker: darken($secondary, 20%);
$secondaryDark: darken($secondary, 10%);
$secondaryLight: lighten($secondary, 10%);
$secondaryLighter: lighten($secondary, 20%);

$neutral: #999;
$neutralDarker: darken($neutral, 20%);
$neutralDark: darken($neutral, 10%);
$neutralLight: lighten($neutral, 10%);
$neutralLighter: lighten($neutral, 20%);

$tertiary: #000;
$tertiaryDarker: darken($tertiary, 20%);
$tertiaryDark: darken($tertiary, 10%);
$tertiaryLight: lighten($tertiary, 10%);
$tertiaryLighter: lighten($tertiary, 20%);

$accent: #000;
$accentDarker: darken($accent, 20%);
$accentDark: darken($accent, 10%);
$accentLight: lighten($accent, 10%);
$accentLighter: lighten($accent, 20%);

$color-white: #fff;
$color-black: #000;
$color-gray: #909090;
$color-red: #d20415;
$color-green: #008000;
