// Variables
$top-alert-bar__color-bg: $palette-tree-poppy !default;
$top-alert-bar__color-hover-bg: $palette-tahiti-gold !default;
$top-alert-bar__color-text: $palette-white !default;

$top-alert-bar__metric-height: 40px !default;

$top-alert-bar__text-font-family: $font-primary !default;
$top-alert-bar__text-font-size: 15px !default;
$top-alert-bar__text-font-weight: $font-weight-semi-bold !default;

// Styles
.top-alert-bar {
  height: $top-alert-bar__metric-height;

  &__sticky {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: $top-alert-bar__metric-height;
    z-index: 1;
  }

  &__link {
    display: flex;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    justify-content: center;
    align-items: center;
    background: $top-alert-bar__color-bg;
    color: $top-alert-bar__color-text;
    font-family: $top-alert-bar__text-font-family;
    font-size: $top-alert-bar__text-font-size;
    font-weight: $top-alert-bar__text-font-weight;
    transition: $transition-normal;

    &:hover,
    &:focus {
      background: $top-alert-bar__color-hover-bg;
      color: $top-alert-bar__color-text;
      text-decoration: none;
    }
  }
}
