// Variables
$search-box__color-style-light-bg: $palette-white !default;
$search-box__color-style-light-text: $palette-boulder !default;
$search-box__color-style-light-icon: $palette-cod-gray !default;
$search-box__color-style-light-border-hover: $palette-pistachio !default;
$search-box__color-style-dark-bg: $palette-gallery !default;
$search-box__color-style-dark-text: $palette-cod-gray !default;
$search-box__color-style-dark-icon: $palette-pistachio !default;
$search-box__color-style-dark-border-hover: $palette-pistachio !default;

$search-box__metric-size-small-height: 34px !default;
$search-box__metric-size-small-padding-h: 14px !default;
$search-box__metric-size-small-border-radius: 17px !default;
$search-box__metric-size-small-icon-margin: 26px !default;
$search-box__metric-size-large-height: 60px !default;
$search-box__metric-size-large-padding-h: 30px !default;
$search-box__metric-size-large-border-radius: 30px !default;
$search-box__metric-size-large-icon-margin: 30px !default;

$search-box__text-font-family: $font-primary !default;
$search-box__text-size-small-font-size: 14px !default;
$search-box__text-size-large-font-size: 17px !default;

// Styles
.search-box {
  display: inline-block;
  position: relative;
  font-family: $search-box__text-font-family;

  &__icon {
    display: flex;
    height: 100%;
    position: absolute;
    justify-content: center;
    align-items: center;
    z-index: 1;

    &--size-small {
      left: $search-box__metric-size-small-padding-h;
    }

    &--size-large {
      left: $search-box__metric-size-large-padding-h;
    }

    &--style-light {
      color: $search-box__color-style-light-icon;
    }

    &--style-dark {
      color: $search-box__color-style-dark-icon;
    }
  }

  &__input-container {
    border: 2px solid;
    transition: $transition-normal;

    &--style-light {
      background: $search-box__color-style-light-bg;
      border-color: $search-box__color-style-light-bg;

      &:hover,
      &:focus-within {
        border-color: $search-box__color-style-light-border-hover;
      }
    }

    &--style-dark {
      background: $search-box__color-style-dark-bg;
      border-color: $search-box__color-style-dark-bg;

      &:hover,
      &:focus-within {
        border-color: $search-box__color-style-dark-border-hover;
      }
    }

    &--size-small {
      height: $search-box__metric-size-small-height;
      border-radius: $search-box__metric-size-small-border-radius;
    }

    &--size-large {
      height: $search-box__metric-size-large-height;
      border-radius: $search-box__metric-size-large-border-radius;
    }
  }

  &__input {
    display: block;
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;

    &:hover,
    &:focus {
      outline: none;
    }

    &--style-light {
      color: $search-box__color-style-light-text;
      &::placeholder {
        color: $search-box__color-style-light-text;
      }
    }

    &--style-dark {
      color: $search-box__color-style-dark-text;
      &::placeholder {
        color: $search-box__color-style-dark-text;
      }
    }

    &--size-small {
      font-size: $search-box__text-size-small-font-size;
      padding: 0 $search-box__metric-size-small-padding-h;
    }

    &--size-large {
      font-size: $search-box__text-size-large-font-size;
      padding: 0 $search-box__metric-size-large-padding-h;
    }

    &--size-small-with-icon {
      padding-left: $search-box__metric-size-small-padding-h + $search-box__metric-size-small-icon-margin;
    }

    &--size-large-with-icon {
      padding-left: $search-box__metric-size-large-padding-h + $search-box__metric-size-large-icon-margin;
    }
  }
}
